import React, { useEffect, useMemo, useState } from "react";
import {
  Drawer,
  List,
  ListItemButton,
  ListItemText,
  Collapse,
  useMediaQuery,
  useTheme,
  Box,
  Avatar,
  Typography,
} from "@mui/material";
import { ExpandLess, ExpandMore, Dashboard, Assessment, Groups, Payment, Inventory, HelpOutline, Settings, Category as CategoryIcon } from "@mui/icons-material";
import { Link, useLocation } from "react-router-dom";
import { BiGrid } from "react-icons/bi";
import { MdArrowRight } from "react-icons/md";
import { baseUrlImage, mobileNumberFilter } from "../../../baseUrl";
import { imageLogo } from "../../../asesets";
const menuIcons = {
  Dashboard: <Dashboard />,
  "All services": <CategoryIcon />,
  Reports: <Assessment />,
  Networks: <Groups />,
  Payments: <Payment />,
  Package: <Inventory />,
  "Help & Support": <HelpOutline />,
  Settings: <Settings />,
};

const AsideAdmin = ({ services, isSidebarOpen, setIsSidebarOpen }) => {
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down("md"));
  const location = useLocation();

  const userPermission = localStorage.getItem("userType");
  const userNumber = localStorage.getItem("regisNumber");
  const [openMenus, setOpenMenus] = useState({});
  const toggleMenu = (item) => setOpenMenus((prev) => ({ ...prev, [item]: !prev[item] }));
  const menuItems = useMemo(() => [
    {
      item: "Dashboard",
      permission: [
        "Retailer",
        "Distributor",
        "Super Distributor",
        "asm",
        "zsm",
        "Api User",
        "Reseller",
        "reseller partner api user",
      ],
      route: "/Dashboard",
    },

    { item: "All services", permission: ["Retailer", "Api User"] },
    {
      item: "Reports",
      permission: ["Retailer",
        "Distributor",
        "Super Distributor",
        "asm",
        "zsm", "Reseller", "reseller partner api user", "Api User",],
      children: [
        {
          item: "Pay In Wallet Report",
          permission: ["reseller partner api user", "Api User",],
          route: "/pay-in-wallet-report",
        },
        {
          item: "Pay Out Report",
          permission: ["reseller partner api user",],
          route: "/pay-out-report",
        },

        {
          item: "Transfer PayIn into Main Report",
          permission: ["reseller partner api user", "Api User",],
          route: "/payin-wallet-to-payout-wallet-report",
        },

        {
          item: "Wallet Transaction Report",
          permission: ["Retailer",
            "Distributor",
            "Super Distributor",
            "asm",
            "zsm", "Reseller", "Api User", "reseller partner api user"],
          route: "/wallet-report",
        },
        {
          item: "AEPS Ledger Report",
          permission: ["Retailer"],
          route: "/aeps-wallet",
        },
        {
          item: "Bank Transfer Report",
          permission: ["Retailer"],
          route: "/dmt_report_search",
        },
        {
          item: "AEPS Transaction Report",
          permission: ["Retailer"],
          route: "/aeps-transaction-report",
        },
        {
          item: "Aadhaar Pay Report",
          permission: [""],
          route: "/aadhaarpay_pay_report",
        },

        {
          item: "CMS Report",
          permission: [""],
          route: "/airtel-cms-report",
        },
        {
          item: "Aeps Payout Report",
          permission: ["Retailer"],
          route: "/Aeps_report",
        },
        {
          item: "Utility Transaction Report",
          permission: ["Retailer"],
          route: "/utility-report",
        },
        {
          item: 'Courier Report',
          permission: ['Retailer'],
          mobilePermission: true,
          route: "/order-details"
        },
        {
          item: "Bank Transfer Report",
          permission: [
            "Distributor",
            "Super Distributor",
            "asm",
            "zsm",],
          route: "/super/bank-transfer-report"
        },
        {
          item: "AEPS Report",
          permission: [
            "Distributor",
            "Super Distributor",
            "asm",
            "zsm",],
          route: "/super/aeps-transfer-report"
        },
        {
          item: "Utility Report",
          permission: [
            "Distributor",
            "Super Distributor",
            "asm",
            "zsm",],
          route: "/super/bbps-report"
        },
        {
          item: "Fund Transfer Report",
          permission: [
            "Distributor",
            "Super Distributor",
            "asm",
            "zsm",],
          route: "/super/fund-transfer-report"
        },


      ],
    },
    {
      item: "Networks",
      permission: ["Distributor", "Super Distributor", "asm", "zsm", "Reseller"],
      children: [
        {
          item: "Downstream",
          permission: ["Distributor", "Super Distributor", "asm", "zsm"],
          route: "/networkzsmasm",
        },
        {
          item: "Users",
          permission: ["Reseller"],
          route: "/reseller-user",
        },
        {
          item: "Earning Report",
          permission: ["Distributor", "Super Distributor"],
          route: "/earning-report",
        },
      ],
    },
    {
      item: "Payments",
      permission: [
        "Retailer",
        "Distributor",
        "Super Distributor",
        "asm",
        "zsm",
        "Api User",
        "reseller partner api user",
        "Reseller"
      ],
      children: [
        {
          item: "Company Bank Details",
          permission: [
            "Retailer",
            "Distributor",
            "Super Distributor",
            "asm",
            "zsm",
            "Api User",
            "Reseller",
            "reseller partner api user"
          ],
          route: "/company-bank-details",
          mobilePermission: true
        },
        {
          item: "ADD Fund Request",
          permission: [
            "Retailer",
            "Distributor",
            "Super Distributor",
            "asm",
            "zsm",
            "Api User",
            "Reseller",
            "reseller partner api user"
          ],
          route: "/add-payment-request",
          mobilePermission: true
        },
        {
          item: "Fund Request History To Company",
          permission: [
            "Retailer",
            "Distributor",
            "Super Distributor",
            "asm",
            "zsm",
            "Api User",
            "Reseller",

          ],
          route: "/payment-request-to-company",
          mobilePermission: true
        },
        {
          item: "Fund request to ZSM/ASM/SD/DS history",
          permission: ["Retailer"],
          route: "/payment-request-to-distributor",
          mobilePermission: true
        },
        {
          item: "Transfer PayIn Wallet to Main Wallet",
          permission: ["reseller partner api user", "Api User"],
          route: "/payin-wallet-to-payout-wallet",
        },
        {
          item: "Fund request to zsm/asm/SD",
          permission: ["Distributor"],
          route: "/payment-request-to-distributor",
        },
        {
          item: "Cashback Settlement",
          permission: ["Retailer"],
          route: "/cashback-settlement",
          mobilePermission: true
        },
        {
          item: "AEPS Wallet Settlement",
          permission: ["Retailer"],
          route: "/aeps-payout-details",
        },


        {
          item: "Fund request to zsm/asm",
          permission: ["Super Distributor"],
          route: "/payment-request-to-distributor",
        },
        {
          item: "Fund request to zsm",
          permission: ["asm"],
          route: "/payment-request-to-distributor",
        },
        {
          item: "Fund request to Sponsor",
          permission: ["reseller partner api user"],
          route: "/payment-request-to-distributor",
        },


        {
          item: "Fund requested By Members",
          permission: ["Distributor", "Super Distributor", "asm", "zsm", "Reseller"],
          route: "/payment-request-to-distributor/super-distributor-history",
        },
        {
          item: "AEPS Wallet to Main Wallet ",
          permission: ["Retailer"],
          route: "/aeps-wallet-transfer",

        },
        {
          item: "AEPS Settlement Details",
          permission: [""],
          route: "/aeps-payout-details",


        },
      ],
    },
    {
      item: "Package",
      permission: ["Retailer", "Api User", "Distributor",
        "Super Distributor",],
      children: [
        {
          item: "Package List",
          permission: ["Retailer", "Api User", "Distributor",
            "Super Distributor",],
          route: "/package-list",
        },
        {
          item: "Package Purchase History",
          permission: ["Distributor",
            "Super Distributor",],
          route: "/package-purchase-history",
        },
        {
          item: "Package Assign",
          permission: ["Distributor",
            "Super Distributor",],
          route: "/package-assign",
        },
        {
          item: "Package History",
          permission: ["Retailer", "Api User"],
          route: "/package-history",
        },


      ],
    },
    {
      item: "Pan Service",
      permission: ["ffhg"],
      hidden: true,
      route: "/pan-service",
    },
    {
      item: "Help & Support",
      permission: [
        "Retailer",
        "Distributor",
        "Super Distributor",
        "asm",
        "zsm",
        "Api User",
        "Reseller", "reseller partner api user"
      ],
      children: [
        {
          item: "Add Ticket",
          permission: [
            "Retailer",
            "Distributor",
            "Super Distributor",
            "asm",
            "zsm",
            "Api User",
            "Reseller", "reseller partner api user"
          ],
          route: "/add-ticket",
        },
        {
          item: "List Ticket",
          permission: [
            "Retailer",
            "Distributor",
            "Super Distributor",
            "asm",
            "zsm",
            "Api User",
            "Reseller", "reseller partner api user"
          ],
          route: "/list-tickets",
        },
        {
          item: "Pending Tickets",
          permission: [

          ],
          route: "/open-dispute",
        },
        {
          item: "Closed Tickets",
          permission: [

          ],
          route: "/clossing-Dispute",
        },
      ],
    },
    {
      item: "Settings",
      permission: [

      ],
      children: [
        {
          item: "Billing Address (should be hide)",
          permission: [

          ],
          route: "/billing_Address",
        },
        {
          item: "Password change",
          permission: [
            "Retailer",
            "Distributor",
            "Super Distributor",
            "asm",
            "zsm",
            "Api User"
          ],
          route: "/change_password",
        },
        {
          item: "Change Pin",
          permission: [
            "Retailer",
            "Distributor",
            "Super Distributor",
            "asm",
            "zsm",
            "Api User"
          ],
          route: "/change_txnpassword",
        },
      ],
    },
  ],[]);

  const servicesSubMenu = services?.map(s => ({
    item: s.service_name,
    icon: s.icon,
    permission: ["Retailer"],
    route: s.route || "#",
  }));
  const allServicesMenu = menuItems.find(m => m.item === "All services");
  if (allServicesMenu) allServicesMenu.children = servicesSubMenu;

  const filteredMenuItems = useMemo(() => {
    return menuItems
      .map(menu => {
        if (!menu.permission.includes(userPermission)) return null;
        let children = menu.children?.filter(c => c.permission.includes(userPermission));
        if ((menu.item === "Reports" || menu.item === "Payments") && mobileNumberFilter.includes(userNumber)) {
          children = menu.children?.filter(c => c.mobilePermission);
        }
        return (menu.children && (!children || children.length === 0)) ? null : { ...menu, children };
      })
      .filter(Boolean);
  }, [menuItems, userPermission]);

  useEffect(() => {
    const activeMenus = {};
    filteredMenuItems.forEach(menu => {
      if (menu.children?.some(c => location.pathname.startsWith(c.route))) activeMenus[menu.item] = true;
    });
    setOpenMenus(activeMenus);
  }, [location.pathname, filteredMenuItems]);

  const renderMenuItem = (menu) => (
    <Box key={menu.item}>
      {menu.children ? (
        <>
          <ListItemButton
            onClick={() => toggleMenu(menu.item)}
            sx={{ color: "#ffffff", "&:hover": { bgcolor: "var(--bs-primary3)" }, py: 1.2, px: 2 }}
          >
            <Box mr={2}>{menuIcons[menu.item] || <BiGrid />}</Box>
            <ListItemText primary={menu.item} primaryTypographyProps={{ fontSize: 14, fontWeight: 500, color: "#ffffff" }} />
            {openMenus[menu.item] ? <ExpandLess sx={{ color: "#ffffff" }} /> : <ExpandMore sx={{ color: "#ffffff" }} />}
          </ListItemButton>
          <Collapse in={openMenus[menu.item]} timeout="auto" unmountOnExit>
            <List component="div" disablePadding>
              {menu.children.map(child => (
                <Link to={child.route} key={child.item} onClick={() => setIsSidebarOpen(false)} style={{ textDecoration: "none" }}>
                  <ListItemButton
                    sx={{
                      pl: 6, py: 1, color: "#ffffff",
                      bgcolor: location.pathname === child.route ? "#fbbf24" : "transparent",
                      borderRadius: 1, mb: 0.5, "&:hover": { bgcolor: "var(--bs-primary3)" }
                    }}
                  >
                    {child.icon ? <Avatar src={baseUrlImage + child.icon} sx={{ width: 20, height: 20, mr: 2 }} /> : <MdArrowRight style={{ marginRight: 8, color: "#ffffff" }} />}
                    <ListItemText primary={child.item} primaryTypographyProps={{ fontSize: 13, fontWeight: 400, color: "#ffffff" }} />
                  </ListItemButton>
                </Link>
              ))}
            </List>
          </Collapse>
        </>
      ) : (
        <Link to={menu.route || "#"} onClick={() => setIsSidebarOpen(false)} style={{ textDecoration: "none" }}>
          <ListItemButton
            sx={{
              color: "#ffffff", "&:hover": { bgcolor: "var(--bs-primary3)" }, py: 1.2, px: 2,
              mb: 0.5, borderRadius: 1, bgcolor: location.pathname === menu.route ? "#fbbf24" : "transparent"
            }}
          >
            <Box mr={2}>{menuIcons[menu.item] || <BiGrid />}</Box>
            <ListItemText primary={menu.item} primaryTypographyProps={{ fontSize: 14, fontWeight: 500, color: "#ffffff" }} />
          </ListItemButton>
        </Link>
      )}
    </Box>
  );

  return (
    <Drawer
      variant="temporary"
      open={isSidebarOpen}
      onClose={() => setIsSidebarOpen(false)}
      anchor="left"
      ModalProps={{ keepMounted: true, onBackdropClick: () => setIsSidebarOpen(false) }}
      PaperProps={{ sx: { width: 280, backgroundColor: "var(--bs-primary3)", color: "#ffffff", borderRight: "1px solid #374151", transition: "all 0.3s" } }}
    >
      <Box display="flex" flexDirection="column" height="100%">
        {/* Logo */}
        <Box display="flex" justifyContent="center" alignItems="center" py={2} bgcolor="var(--bs-primary3)"borderBottom="1px solid #374151">
          <Link to="/Dashboard">
            <img src={imageLogo} alt="Logo" style={{ height: 50, objectFit: "contain" }} />
          </Link>
        </Box>

        {/* Menu */}
        <List sx={{ flexGrow: 1 }}>
          {filteredMenuItems.map(renderMenuItem)}
        </List>

        {/* Footer */}
        <Box px={2} py={2} borderTop="1px solid #374151" display="flex" alignItems="center">
          <Avatar sx={{ bgcolor: "#fbbf24", width: 32, height: 32, mr: 1 }}>{userPermission?.[0]}</Avatar>
          <Box>
            <Typography variant="body2" fontWeight={500} color="#ffffff">{userPermission}</Typography>
            <Typography variant="caption" color="#d1d5db">{userNumber}</Typography>
          </Box>
        </Box>
      </Box>
    </Drawer>
  );
};

export default AsideAdmin;